.input {
    padding: 0;
    width: 200px;
    height: 20px;
    border: none;
    outline: none;
    background-color: var(--bg-content);
    color: var(--font-grey);
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    font-weight: 700;
    font-size: 17px;
    font-style: normal;
    letter-spacing: normal;
}

.p {
    margin: 0;
    padding: 0;
    color: var(--font-grey-disactive);
}